<i18n>
{
  "en": {
    "ShopDetails": "ShopDetails",
    "Time": "Business Hours",
    "ConsumptionPerPerson": "Consumption per person",
    "ShopAddress": "Shop address",
    "address": "Zhongshan avenue tangxia",
    "m": "{count}km",
    "Scanning": "Scanning two-dimensional code charging",
    "Charger": "Charger",
    "LeasedChargingTreasure": "Leased charging treasure",
    "Borrow": "Borrow {count}",
    "Repay": "Repay {count}",
    "Amap": "Amap",
    "BaiduMaps": "Baidu Maps",
    "Cancel": "Cancel"
  },
  "zh": {
    "ShopDetails": "店铺详情",
    "Time": "营业时间",
    "ConsumptionPerPerson": "人均消费",
    "ShopAddress": "店铺地址",
    "address": "中山大道棠下",
    "m": "{count}km",
    "Scanning": "扫码充电",
    "Charger": "提供座充",
    "LeasedChargingTreasure": "租借充电宝",
    "Borrow": "可借{count}台",
    "Repay": "可还{count}台",
    "Amap": "高德地图",
    "BaiduMaps": "百度地图",
    "Cancel": "取消"
  }
}
</i18n>
<template>
  <div class="store-details">
    <head-top :title='$t("ShopDetails")' :go-back="true" @goBackHander="goBackHander"></head-top>
    <div class="container">
      <div class="pic">
        <span>
          <!-- <img :src="data.logo || require('../../assets/global_images/timg.jpg')" /> -->
          <img src="../../assets/global_images/timg.jpg" />
        </span>
      </div>
      <div class="details bomPixel">
        <ul class="left">
          <li>
            <img src="../../assets/store_images/time-icon.png" alt="">
            <span>{{ $t("Time") }}</span>
            <strong>{{data.openTime}}</strong>
          </li>
          <li>
            <img src="../../assets/store_images/user.png" alt="">
            <span>{{ $t("ConsumptionPerPerson") }}</span>
            <strong>￥{{data.consumption}}</strong>
          </li>
          <li>
            <img src="../../assets/store_images/add-icon.png" alt="">
            <span>{{ $t("ShopAddress") }}</span>
            <strong>{{data.address}}</strong>
          </li>
        </ul>
        <div class="right">
          <div class="navigation" @click="navigation">
            <img src="../../assets/store_images/nav.png" alt="" v-if="Version === 1">
            <img src="../../assets/synerMax-skin/navigation@3x.png" alt="" v-if="Version === 2">
            <span>{{ $t("m",{count: data.distance / 1000}) }}</span>
          </div>
        </div>
      </div>
      <div class="column">
        <div class="charger" v-if="data.type === 2 || data.type === 3">
          <img class="line" src="../../assets/store_images/line.png" alt="">
          <span>{{ $t("Scanning") }}</span>
          <strong>
            <img src="../../assets/store_images/charger-default.png" alt="">
            <b>{{ $t("Charger") }}</b>
          </strong>
        </div>
        <div class="power" v-if="data.type === 1 || data.type === 3">
          <img class="line" src="../../assets/store_images/line.png" alt="">
          <span>{{ $t("LeasedChargingTreasure") }}</span>
          <strong>
            <img src="../../assets/store_images/baby.png" alt="">
            <b>{{ $t("Borrow",{count: data.fullyCount}) }} {{ $t("Repay",{count: data.vacancyCount}) }}</b>
          </strong>
        </div>
      </div>
    </div>

    <!-- 操作表 -->
    <action-sheet :id="3"
                  :title="$t('address')"
                  :list="[$t('Amap'), $t('BaiduMaps')]"
                  :cancel="$t('Cancel')"
                  color="#4FA3FE"
                  @sheetHandle="sheetHandle"></action-sheet>
  </div>
</template>

<script>
import { mapActions } from 'vuex'
import headTop from '../../components/Header/index'
import actionSheet from '../../components/ActionSheet/index'
export default {
  name: 'store-details',
  data () {
    return {
      data: {} // 详情数据
    }
  },

  components: {
    headTop,
    actionSheet
  },

  mounted () {
    this.data = this.$route.params.data
    console.log(this.data)
  },

  methods: {
    ...mapActions([
      'setGlobalSheet'
    ]),

    goBackHander () {
      // console.log(123)
    },

    // 导航
    navigation () {
      this.setGlobalSheet({bol: true, id: 3})
    },

    sheetHandle (confirm, value, id) {
      if (value === 1) {
        // 高德地图
        // this.dataInteractionRequest({
        //   msgType: '0x5301',
        //   type: 1,
        //   lng: this.data.lng,
        //   lat: this.data.lat,
        //   name: this.data.name,
        //   address: this.data.address
        // })
      } else if (value === 2) {
        // 百度地图
        // this.dataInteractionRequest({
        //   msgType: '0x5301',
        //   type: 2,
        //   lng: this.data.lng,
        //   lat: this.data.lat,
        //   name: this.data.name,
        //   address: this.data.address
        // })
      }
      this.setGlobalSheet({bol: false, id: 0})
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style lang="less" scoped>
.store-details{
  .fs;
  background: #FFF;
  .container{
    .fs-noTop;
    font-size: 26px;
    .pic{
      .wh(690px, 400px);
      margin: 30px auto;
      span{
        .wh(100%, 100%);
        display: block;
        background: #CCC;
        border-radius: 20px;
        position: relative;
        img{
          .wh(100%, 100%);
          position: absolute;
          left: 0;
          top: 0;
          border-radius: 20px;
        }
      }
    }
    .details{
      .wh(100%, 230px);
      position: relative;
      .left{
        .wh-f(76%, 100%);
        text-align: left;
        padding-left: 4%;
        li{
          .wh(100%, 75px);
          img{
            height: 32px;
            position: relative;
            top: 10px;
          }
          span{
            color: #585858;
            margin: 0 10px 0 20px;
          }
          strong{
            color: #ABABAB;
            font-size: 30px;
          }
        }
        li:nth-child(3) {
          display: flex;
          span{
            margin: 0 20px 0 30px;
            line-height: 50px;
          }
          strong{
            flex: 1;
            font-size: 26px;
            .text-over;
            line-height: 50px;
          }
        }
      }
      .right{
        .wh-f(24%, 100%);
        position: relative;
        .navigation{
          .wh(160px, 120px);
          .f-center;
          img{
            height: 60px;
            .f-center;
            top: 30%;
          }
          span{
            .f-center;
            top: 70%;
          }
        }
      }
    }
    .column{
      .wh(100%, auto);
      text-align: left;
      .charger, .power{
        .wh-f(auto, 146px);
        position: relative;
        .line{
          height: 70px;
          position: absolute;
          left: 28px;
          top: 50%;
          margin-top: -35px;
        }
        span{
          .wh(100%, 50%);
          line-height: 90px;
          display: block;
          font-size: 30px;
          padding-left: 70px;
        }
        strong{
          .wh(100%, 50%);
          line-height: 50px;
          display: block;
          padding-left: 70px;
          img{
            height: 40px;
            position: relative;
            top: 10px;
            left: 0;
          }
          b{
            margin-left: 8px;
            color: #AAAAAA;
          }
        }
      }
      .charger{
        margin-right: 40px;
      }
    }
  }
}
</style>



